// import "@babel/polyfill";
import "babel-polyfill";

import Vue from "vue";
import store from "./store";
// init dynamic modules after store init
import "@/store/modules";
import "./plugins/vuetify";
import "./plugins/meta";
import "./plugins/misc";
import i18n from "./plugins/i18n";
import App from "./App.vue";
import router from "./router";
import "roboto-fontface/css/roboto/roboto-fontface.css";
import "@mdi/font/css/materialdesignicons.css";
import { AxiosResponse, AxiosError } from "axios";
import {
  IServerResponse,
  INotification
} from "./store/modules/NotificationModule";
import { notificationModule } from "@/store/modules";

Vue.config.productionTip = false;

Vue.prototype.$bNotify = function(
  notification:
    | AxiosResponse<IServerResponse>
    | AxiosError
    | IServerResponse
    | INotification[]
    | INotification
) {
  notificationModule.addFromApiResponse(notification);
};

new Vue({
  router,
  store,
  i18n,
  render: h => h(App)
}).$mount("#app");
