import store from "@/store";
import { Module, VuexModule, Action, Mutation } from "vuex-module-decorators";
import Vue from "vue";
import { AxiosResponse, AxiosError } from "axios";

@Module({ dynamic: true, store, name: "notifications", namespaced: true })
export default class SettingsModule extends VuexModule {
  private notifications: INotification[] = [];

  get notificationHistory() {
    return [...this.notifications].reverse();
  }
  get currentNotification(): INotification | false {
    const unseenNotifications = this.notifications.filter(n => !n.seen);
    return unseenNotifications[0] || false;
  }

  @Mutation
  add(notification: INotification) {
    this.notifications.push(notification);
    // limit notifications to 50
    if (this.notifications.length > 50) {
      this.notifications.shift();
    }
    this.notifications = this.notifications;
  }

  @Mutation
  closeCurrent() {
    for (let i = 0; i < this.notifications.length; i++) {
      if (!this.notifications[i].seen) {
        Vue.set(this.notifications[i], "seen", true);
        this.notifications = this.notifications; // temporary
        return;
      }
    }
    this.notifications = this.notifications; // temporary
  }

  @Mutation
  addFromApiResponse(
    data:
      | AxiosResponse<IServerResponse>
      | AxiosError
      | IServerResponse
      | INotification[]
      | INotification
  ) {
    let messages: INotification[] = [];
    const anyData: any = data;
    if (anyData.data) {
      messages = (anyData as AxiosResponse<IServerResponse>).data.messages.map(
        m => ({
          type: m.type || NotificationType.info,
          name: m.name,
          args: m.args,
          seen: false,
          key: Math.random()
        })
      );
    } else if (anyData.response && anyData.response.data) {
      messages = ((anyData as AxiosError).response!
        .data as IServerResponse).messages.map(m => ({
        type: m.type || NotificationType.info,
        name: m.name,
        args: m.args,
        seen: false,
        key: Math.random()
      }));
    } else if (anyData.messages) {
      messages = (anyData as IServerResponse).messages.map(m => ({
        type: m.type || NotificationType.info,
        name: m.name,
        args: m.args,
        seen: false,
        key: Math.random()
      }));
    } else if (anyData.length && anyData.map) {
      messages = (anyData as INotification[]).map(m => ({
        type: m.type || NotificationType.info,
        name: m.name,
        args: m.args,
        seen: false,
        key: Math.random()
      }));
    } else if (
      anyData.config &&
      anyData.config.data &&
      anyData instanceof Error
    ) {
      messages = [
        {
          type: NotificationType.error,
          name: "api.connection-not-available",
          seen: false,
          key: Math.random()
        }
      ];
    } else if (anyData.name) {
      messages = [
        {
          ...(anyData as INotification),
          type: anyData.type || NotificationType.info,
          seen: false,
          key: Math.random()
        }
      ];
    } else {
      console.log(anyData);
      alert("Unknown error");
    }
    // messages = data.response.data.messages; // TODO
    if (messages && messages.length) {
      for (const message of messages) {
        this.add(message);
      }
    }
  }
}

enum NotificationType {
  success = "success",
  error = "error",
  info = "info",
  warning = "warning"
}

export interface INotification {
  type: NotificationType;
  name: string;
  args?: { [index: string]: string };
  seen: boolean;
  key: number;
}

export interface IServerResponse {
  [index: string]: any;
  messages: ({
    name: string;
    args?: { [index: string]: string };
    type: NotificationType;
  })[];
}
