import store from "@/store";
import { Module, VuexModule, Action, Mutation } from "vuex-module-decorators";
import Vue from "vue";
import { AxiosResponse, AxiosError } from "axios";
import * as api from "../api";

@Module({ dynamic: true, store, name: "auth", namespaced: true })
export default class SettingsModule extends VuexModule {
  private token = "";
  private _username = "";
  private _anyUserExists = true;

  get isLoggedIn() {
    return !!(this.token && this.username);
  }
  get username() {
    return this._username;
  }
  get anyUserExists() {
    return this._anyUserExists;
  }

  @Mutation
  setServerResponse({ token, username }: { token: string; username: string }) {
    this.token = token;
    this._username = username;
  }
  @Mutation
  deleteAuthData() {
    this.token = this._username = "";
  }
  @Mutation
  setAnyUserExists(exists: boolean) {
    this._anyUserExists = exists;
  }

  @Action({})
  async authenticate({
    password,
    username
  }: {
    password: string;
    username: string;
  }) {
    const data = await api.login({ username, password });
    this.setServerResponse({
      token: (data as any).payload.token, // TODO:
      username
    });
  }
  @Action({})
  async logout() {
    // TOOD: tell the server to invalidate the token
    await wait(500);
    this.deleteAuthData();
    // TODO: also delete jobs, repositories and settings still saved in the store
  }
  @Action({})
  async createUser({
    password,
    username,
    firstName,
    lastName,
    email
  }: {
    password: string;
    username: string;
    firstName: string;
    lastName: string;
    email: string;
  }) {
    const data = await api.createUser({
      password,
      username,
      firstName,
      lastName,
      email
    });
    return data;
  }
  @Action
  async checkIfAnyUserExists() {
    const data = await api.anyUserExists();
    this.setAnyUserExists(data.exists); // TODO
    return data; // TODO: proper response
  }
}

const wait = (time: number) =>
  new Promise(resolve => setTimeout(resolve, time));
